import { AngularSvgIconModule } from 'angular-svg-icon';
import { CommonModule } from '@angular/common';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { MenuItem } from 'primeng/api';
import { StepperModule } from 'primeng/stepper';
import { StepsModule } from 'primeng/steps';

@Component({
  selector: 'pla-stepper',
  imports: [CommonModule, StepperModule, StepsModule, AngularSvgIconModule],
  templateUrl: './pla-stepper.component.html',
  styleUrl: './pla-stepper.component.scss',
})
export class PlaStepperComponent {
  @Input() activeStep!: number;
  @Input() activeIndex!: number;
  @Input() stepItems: MenuItem[] = [];
  @Input() formValid: any;
  @Input() showValidationErrors: boolean = false;

  @Output() activeIndexChange = new EventEmitter<number>();

  constructor() {}

  isStepInvalid(id: string): boolean {
    if (!this.showValidationErrors) return false;

    const found = this.formValid.find((f: any) => f.id === id);
    return found ? !found.isValid : false;
  }

  onStepClick(activateCallback: () => void, index: number): void {
    activateCallback(); // trigger PrimeNG's step activation
    this.activeIndexChange.emit(index);
  }
}
