import { CommonModule } from '@angular/common';
import { Component, EventEmitter, Input, Output } from '@angular/core';
import { ButtonModule } from 'primeng/button';

@Component({
  selector: 'pla-button-outlined',
  imports: [ButtonModule, CommonModule],
  templateUrl: './pla-button-outlined.component.html',
  styleUrl: './pla-button-outlined.component.css'
})
export class PlaButtonOutlinedComponent {
  @Input() disabled = false;
  @Input() label: string = '';
  @Input() icon: string = '';
  @Input() styleClass: string = '';   // ใช้สำหรับ CSS class
  @Input() style: { [klass: string]: any } = {}; //ใช้สำหรับ inline style
  @Output() onClick = new EventEmitter<void>();
  @Input() iconPos: 'left' | 'right' | 'top' | 'bottom' = 'left';

  get mergedStyle() {
    return { ...this.style, color: '#000' };
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  onActionClick() {
    this.onClick.emit();
  }
}
