import { Component, Input, forwardRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR, ReactiveFormsModule } from '@angular/forms';
import { FloatLabelModule } from 'primeng/floatlabel';
import { InputText } from 'primeng/inputtext';

@Component({
  selector: 'pla-input-text',
  imports: [InputText, FloatLabelModule, ReactiveFormsModule],
  templateUrl: './pla-input-text.component.html',
  styleUrl: './pla-input-text.component.scss',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => PlaInputText),
      multi: true
    }
  ]
})
export class PlaInputText implements ControlValueAccessor {
  @Input() label: string = '';
  @Input() type: string = 'text';
  @Input() class: string = '';
  @Input() style = {};

  value: any = '';
  disabled = false;

  onChange: any = () => { };
  onTouched: any = () => { };

  writeValue(value: any): void {
    this.value = value ?? '';
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  onInputChange(event: any): void {
    this.value = event.target.value;
    this.onChange(this.value);
    this.onTouched();
  }
}
