import { CommonModule } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import {
  FormArray,
  FormBuilder,
  FormGroup,
  ReactiveFormsModule,
} from '@angular/forms';
import { ButtonModule } from 'primeng/button';
import { DividerModule } from 'primeng/divider';
import { PlaDynamicForm } from '../pla-dynamic-form/pla-dynamic-form.component';

@Component({
  selector: 'pla-form-input-array',
  imports: [
    CommonModule,
    ReactiveFormsModule,
    DividerModule,
    ButtonModule,
    PlaDynamicForm,
  ],
  templateUrl: './pla-form-input-array.component.html',
  styleUrl: './pla-form-input-array.component.scss',
})
export class PlaFormInputArrayComponent implements OnInit {
  @Input() pageState: 'View' | 'Edit' = 'Edit';
  @Input() form!: FormGroup; // <- parent form
  @Input() formArrayName!: string;
  @Input({ required: true }) createItemFn!: () => FormGroup;
  @Input({ required: true }) formSections: any[] = [];
  @Input() isSubmitted = false;
  @Input() isLoadingPackage = false;

  isSmallScreen = false;

  constructor(private readonly formBuilder: FormBuilder) {}

  ngOnInit(): void {
    const mq = window.matchMedia('(max-width: 768px)');
    this.isSmallScreen = mq.matches;
    mq.addEventListener('change', (e) => (this.isSmallScreen = e.matches));

    if (this.createItemFn && typeof this.createItemFn !== 'function') {
      throw new Error('createItemFn must be a function');
    }
  }

  // Use the parent's FormArray
  get items(): FormArray {
    return this.form.get(this.formArrayName) as FormArray<FormGroup>;
  }

  getNestedFormGroup(i: number, key: string): FormGroup {
    const group = this.items.at(i) as FormGroup;
    return group.get(key) as FormGroup;
  }

  addItem(): void {
    const newItem = this.createItemFn
      ? this.createItemFn()
      : this.defaultCreateItem();
    this.items.push(newItem);
  }

  removeItem(index: number): void {
    this.items.removeAt(index);
  }

  // optional fallback if parent doesn't pass createItemFn
  private defaultCreateItem(): FormGroup {
    return this.formBuilder.group({});
  }

  onSubmit(): void {
    console.log(this.form.value);
  }

  getControls(): FormGroup[] {
    return this.items.controls as FormGroup[];
  }
}
