import { CommonModule } from '@angular/common';
import { Component, Input } from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { FloatLabelModule } from 'primeng/floatlabel';
import { CheckboxModule } from 'primeng/checkbox';
import { IFormControlDynamic } from '../pla-dynamic-form/form.interface';
import { isRequired, classValidate } from '../../utils/pla-utils';

@Component({
  selector: 'pla-form-checkbox',
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FloatLabelModule,
    CheckboxModule,
  ],
  templateUrl: './pla-form-checkbox.component.html',
  styleUrl: './pla-form-checkbox.component.scss',
})
export class PlaFormCheckboxComponent {
  @Input({ required: true }) listFormGroup!: FormGroup;
  @Input({ required: true }) control!: IFormControlDynamic;
  @Input({ required: true }) submitted: boolean = false;

  isRequired: Function = isRequired;
  classValidate: Function = classValidate;
}
