@Injectable({
  providedIn: 'root',
})
export class LoginService {
  private readonly isLoggedInSubject = new BehaviorSubject<boolean>(false);
  isLoggedIn$ = this.isLoggedInSubject.asObservable();

  constructor(
    // private readonly state: StateManagementService,
    private readonly http: HttpClient,
    // private readonly cookiesServices: CookiesServices,
    // private readonly router: Router
  ) {}


  refreshToken(): Observable<any> {
    const url: string = "https://portalapi-dev.philliplife.com/uam/api/" + 'v1.1/Authorization/refresh-token';
    const clientId = "f8d4aced-ad61-4191-8648-78da41f48c56";
    const _httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'X-ClientId': clientId,
      }),
    };

    const data = {
      targetClientId: clientId,
    };
    return this.http.post<string>(url, data, _httpOptions);
  }

  verifyToken(): Observable<any> {
    return this.http
      .get<any>(
        "https://portalapi-dev.philliplife.com/uam/api/" + `v1.1/Authorization/verify-token`
      )
      .pipe(tap((res) => this.isLoggedInSubject.next(res.isValid)));
  }

  uamAuthentication(body: any): Observable<any> {
    return this.http.post<any>(
      "https://portalapi-dev.philliplife.com/uam/api/" + 'v1/authentication/login',
      body
    );
  }

  checkAuth(): boolean {
    return this.isLoggedInSubject.getValue();
  }
}
