export class TYPE {
  public static readonly TEXT = 'text';
  public static readonly NUMBER = 'number';
  public static readonly DECIMAL = 'decimal';
  public static readonly INPUTGROUP = 'input-group';
  public static readonly SELECTBUTTON = 'select-button';

  public static readonly TEXTAREA = 'textarea';
  public static readonly DROPDOWN = 'dropdown';
  public static readonly SELECT = 'select';
  public static readonly RADIO = 'radio';
  public static readonly MULTISELECT = 'multiselect';
  public static readonly CHECKBOX = 'checkbox';
  public static readonly EMPTY = 'empty';

  public static readonly DATEPICKER = 'datepicker';
  public static readonly TOGGLESWITCH = 'toggleswitch';
}

export interface ICommonData {
  code: string | number | string[];
  name?: string;
  name_En?: string;
  desc?: string;
  icon?: string;
  id?: string;
  disabled?: boolean;
}

export interface IFormControlDynamic {
  type?:
    | 'text'
    | 'number'
    | 'decimal'
    | 'textarea'
    | 'dropdown'
    | 'empty'
    | 'select'
    | 'datepicker'
    | 'toggleswitch'
    | 'radio'
    | 'multiselect'
    | 'checkbox'
    | 'input-group'
    | 'select-button';
  value?: any; // used for standalone mode
  formControlName?: string;
  subFormControlName?: string;
  selectControlName?: string;
  placeholder?: string;
  label?: string;
  rangeLabel?: string[];
  class?: string;
  readOnly?: true;
  classIcon?: string;
  iconPosition?: 'left' | 'right';
  fluid?: boolean;
  hidden?: boolean;
  isViewMode?: boolean;
  loading?: boolean;

  dataLabel?: string; // DROPDOWN AND INFO
  data?: ICommonData[]; // DROPDOWN ONLY
  //   groupData?: IGroupData[];
  optionLabel?: any; // SELECT ONLY
  optionValue?: any; // SELECT ONLY
  filter?: boolean; // SELECT ONLY
  clear?: boolean; // SELECT ONLY
  display?: string; // MULTI-SELECT ONLY
  maxSelectedLabels?: number | null; // MULTI-SELECT ONLY
  disableToggleAll?: boolean; // MULTI-SELECT ONLY

  min?: number; // INPUT NUMBER ONLY
  max?: number; // INPUT NUMBER ONLY
  digit?: number; // INPUT NUMBER ONLY
  msgMaxLength?: string; // MESSAGE VALIDATE MAX LENGTH
  msgMinLength?: string; // MESSAGE VALIDATE MIN LENGTH
  msgRequired?: string; // MESSAGE VALIDATE REQUIRED
  msgPattern?: string; // MESSAGE VALIDATE PATTERN
  msgCustom?: string; // MESSAGE CUSTOMIZE
  suffix?: string; // INPUT
  prefix?: string; // INPUT

  cols?: number; // TEXTAREA ONLY
  rows?: number; // TEXTAREA ONLY

  dateFormat?: string; // DATEPICKER ONLY
  showTime?: boolean; // DATEPICKER ONLY
  hourFormat?: string; // DATEPICKER ONLY
  minDate?: string; // DATEPICKER ONLY
  maxDate?: string; // DATEPICKER ONLY
  selectionMode?: 'single' | 'multiple' | 'range'; // DATEPICKER ONLY
  readonlyInput?: boolean; // DATEPICKER ONLY
  showButtonBar?: boolean; // DATEPICKER ONLY

  stateOptions?: any[]; // INPUTGROUP ONLY

  onBlur?: Function; // RETURN VALUE TO FUNCTION
  onChange?: Function; // RETURN VALUE TO FUNCTION
  onHide?: Function; // RETURN VALUE TO FUNCTION
  completeMethod?: Function;
  onKeyup?: Function; // RETURN VALUE TO FUNCTION
  onClick?: Function; // RETURN VALUE TO FUNCTION
  fieldGroup?: string; // RETURN VALUE TO FUNCTION
  disabled?: boolean;
  onFilter?: Function;
  input?: Function;

  appCharCount?: boolean; // DIRECTIVE CHAR COUNT
  inputTypeCount?: 'input' | 'textarea'; // DIRECTIVE CHAR COUNT

  floatingSuffix?: string; // DIRECTIVE FLOATING SUFFIX

  trueValue?: any; // TOGGLESWITCH ONLY
  falseValue?: any; // TOGGLESWITCH ONLY
}
