import { CommonModule } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { FloatLabelModule } from 'primeng/floatlabel';
import { SelectModule } from 'primeng/select';

import { IFormControlDynamic } from '../pla-dynamic-form/form.interface';
import { classValidate, isRequired } from '../../utils/pla-utils';

@Component({
  selector: 'pla-form-select',
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FloatLabelModule,
    SelectModule,
  ],
  templateUrl: './pla-form-select.component.html',
  styleUrl: './pla-form-select.component.scss',
})
export class PlaFormSelectComponent implements OnInit {
  @Input({ required: true }) listFormGroup!: FormGroup;
  @Input({ required: true }) control!: IFormControlDynamic;
  @Input({ required: true }) submitted: boolean = false;

  isRequired: Function = isRequired;
  classValidate: Function = classValidate;

  ngOnInit() {}
}
