import { Component, Input, forwardRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR, FormControl, ReactiveFormsModule } from '@angular/forms';
import { FloatLabelModule } from 'primeng/floatlabel';
import { SelectModule } from 'primeng/select';

@Component({
  selector: 'pla-input-select',
  standalone: true,
  imports: [FloatLabelModule, ReactiveFormsModule, SelectModule],
  templateUrl: './pla-input-select.component.html',
  styleUrls: ['./pla-input-select.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => PlaInputSelect),
      multi: true
    }
  ]
})
export class PlaInputSelect implements ControlValueAccessor {
  @Input() drpData: any[] = [];
  @Input() label: string = '';
  @Input() style: { [klass: string]: any } = {};
  @Input() class: string = '';

  innerControl = new FormControl();

  onChange = (_: any) => { };
  onTouched = () => { };

  writeValue(value: any): void {
    this.innerControl.setValue(value, { emitEvent: false });
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
    this.innerControl.valueChanges.subscribe(fn);
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState?(isDisabled: boolean): void {
    isDisabled ? this.innerControl.disable({ emitEvent: false })
      : this.innerControl.enable({ emitEvent: false });
  }
}
