import { AbstractControl, FormGroup, Validators } from '@angular/forms';

export const isRequired = (
  controlName: string,
  lisFormGroup: FormGroup
): boolean => {
  const control: AbstractControl | null = lisFormGroup.get(controlName);
  return control?.hasValidator(Validators.required) ?? false;
};

export const classValidate = (
  formControl: AbstractControl | null,
  submitted = false
): Record<string, boolean> => {
  if (!formControl) return {};

  const isInvalid = formControl.invalid;
  const hasInteracted = formControl.dirty;
  const showError = isInvalid && (submitted || hasInteracted);

  return {
    'ng-invalid': showError,
    'ng-dirty': showError,
  };
};
