import { CommonModule } from '@angular/common';
import { Component, EventEmitter, Input, Output } from '@angular/core';
import { DialogModule } from 'primeng/dialog';
import { PlaButtonOutlinedComponent } from '../pla-button-outlined/pla-button-outlined.component';
import { PlaButtonPrimaryIconComponent } from '../pla-button-primary-icon/pla-button-primary-icon.component';

@Component({
  selector: 'pla-dialog',
  imports: [CommonModule, DialogModule, PlaButtonOutlinedComponent, PlaButtonPrimaryIconComponent],
  templateUrl: './pla-dialog.component.html',
  styleUrl: './pla-dialog.component.css'
})
export class PlaDialogComponent {
  @Input() visible = false;
  @Input() title?: string;
  @Input() message?: string;
  @Input() icon?: string;
  @Input() actionLabel: string = '';
  @Input() width = '450px';
  @Input() height = '396px';

  @Output() visibleChange = new EventEmitter<boolean>();
  @Output() confirm = new EventEmitter<void>();
  @Output() cancel = new EventEmitter<void>();

  onCancel() {
    this.visibleChange.emit(false);
    this.cancel.emit();
  }

  onConfirm() {
    this.confirm.emit();
    this.visibleChange.emit(false);
  }
}
